import ArtyomAssistant from "../2-features/STT/ArtyomAssistant.js"; 

// Controller for managing accessibility tools and interactions
class AccessibilityCompanionController {
    constructor() {
        // Store references to key UI elements and features
        this.elements = {
            title: document.querySelector(".accessibility-title"),
            buttons: {
                tts: document.querySelector('button[aria-label="Text to Speech"]'),
                stt: document.querySelector('button[aria-label="Speech to Text"]'),
                signLanguage: document.querySelector('button[aria-label="Sign Language Translator"]'),
                imageCaption: document.querySelector('button[aria-label="Image Captioning"]')
            },
            speechDisplay: document.getElementById("recognizedText")
        };

        // Initialize assistive technologies
        this.artyomAssistant = new ArtyomAssistant(this);
        
        // Set up the interface
        this.initialize();
    }

    // Initialize the accessibility companion
    initialize() {
        // Set the title from the extension manifest
        if (this.elements.title) {
            this.elements.title.textContent = chrome.runtime.getManifest().name || "Accessibility Companion";
        }

        // Set up event listeners once DOM is fully loaded
        document.addEventListener("DOMContentLoaded", () => {
            this.setupEventListeners();
        });
    }

    // Configure event listeners for accessibility buttons
    setupEventListeners() {
        // Validate button existence before binding
        Object.entries(this.elements.buttons).forEach(([key, button]) => {
            if (button) {
                button.addEventListener("click", this[`handle${key.charAt(0).toUpperCase() + key.slice(1)}`].bind(this));
            } else {
                console.warn(`Button for ${key} not found`);
            }
        });
    }

    // Text-to-Speech functionality
    handleTts() {
        console.log("Text-to-Speech activated");
        this.sendMessageToActiveTab({ action: "extractText" });
    }

    // Speech-to-Text with push-to-talk implementation
    handleStt() {
        console.log("Speech-to-Text initialized with push-to-talk");
        
        const sttButton = this.elements.buttons.stt;
        
        // Implement push-to-talk with Space key
        window.addEventListener("keydown", (event) => {
            if (event.code === "Space" && !this.artyomAssistant.isListening) {
                console.log("Push-to-Talk: Listening activated");
                this.artyomAssistant.startListening();
                sttButton.textContent = "Listening...";
                sttButton.classList.add("active");
            }
        });

        window.addEventListener("keyup", (event) => {
            if (event.code === "Space" && this.artyomAssistant.isListening) {
                console.log("Push-to-Talk: Listening stopped");
                this.artyomAssistant.stopListening();
                sttButton.textContent = "Speech to Text (STT)";
                sttButton.classList.remove("active");
            }
        });
    }

    // Sign Language Translator placeholder
    handleSignLanguage() {
        console.log("Sign Language Translator activated");
        this.showNotification("Sign Language Translator", "Feature coming soon!");
    }

    // Image Captioning placeholder
    handleImageCaption() {
        console.log("Image Captioning activated");
        this.showNotification("Image Captioning", "Feature coming soon!");
    }

    // Send message to current active browser tab
    sendMessageToActiveTab(message) {
        chrome.tabs.query({ active: true, currentWindow: true }, (tabs) => {
            if (tabs.length > 0) {
                chrome.tabs.sendMessage(tabs[0].id, message);
            } else {
                console.warn("No active tab found");
            }
        });
    }

    // Utility method to show user notifications
    showNotification(title, message) {
        const notification = document.createElement('div');
        notification.className = 'notification';
        notification.innerHTML = `
            <h3>${title}</h3>
            <p>${message}</p>
        `;
        document.body.appendChild(notification);
        
        // Remove notification after 3 seconds
        setTimeout(() => {
            document.body.removeChild(notification);
        }, 3000);
    }

    // Programmatically trigger button actions
    triggerButtonAction(action) {
        const actionMap = {
            "tts": this.handleTts,
            "stt": this.handleStt,
            "signLanguage": this.handleSignLanguage,
            "imageCaption": this.handleImageCaption
        };

        const handler = actionMap[action];
        if (handler) {
            handler.call(this);
        } else {
            console.warn(`Unknown action: ${action}`);
        }
    }
}

// Create and export the accessibility companion instance
const accessibilityCompanion = new AccessibilityCompanionController();
export default accessibilityCompanion;